<template>
  <v-card-text>
    <v-row>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="white--text">Текущий участник</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="createUser.NUM_PATH" :items="numPath" label="Номер заезда" dense hide-details dark></v-select>
              </v-col>
              <v-col cols="12">
                <v-select v-model="createUser.PEOPLE_CODE" :items="peoples" item-value="ID" label="Участник" dense hide-details dark>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.ID }}
                  </template>
                  <!-- ---------------------------Изменение элементов в списке--------------------------------- -->
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.ID }}
                  </template>
                </v-select>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addCurrentPeople()" class="mt-4" color="success">Сохранить участника</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="white--text">Финиш участника</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_PATH" :items="numPath" label="Номер заезда" readonly dense hide-details dark></v-select>
              </v-col>
              <v-col cols="12">
                <v-select v-model="editedItem.PEOPLE_CODE" :items="peoples" item-value="ID" label="Участник" readonly dense hide-details dark>
                  <template #[`selection`]="{ item }">
                    {{ "Участник №" + item.ID }}
                  </template>
                  <!-- ---------------------------Изменение элементов в списке--------------------------------- -->
                  <template #[`item`]="{ item }">
                    {{ "Участник №" + item.ID }}
                  </template>
                </v-select>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="editedItem.POINTS" type="number" label="Очки за заезд" dark dense hide-details></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field v-model="editedItem.TIME_MIN" type="number" label="Минуты" dark dense hide-details></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field v-model="editedItem.TIME_SEC" type="number" label="Секунды" dark dense hide-details></v-text-field>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addFinishTimePeople()" class="mt-4" color="success">Сохранить результат</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="8">
        <pre class="white--text">editedItem{{ editedItem }}</pre>
        <pre class="white--text">currentPeople{{ currentPeople }}</pre>
      </v-col>
    </v-row>
  </v-card-text>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  data() {
    return {
      editedItem: {}, //Редактирование участника
      createUser: {}, //Создание участника
    };
  },
  computed: {
    ...mapState("nsi", ["figure", "numPath", "peoples", "numFine", "currentPeople"]),
    tableHeight() {
      return window.innerHeight - 120;
    },
    isEditForm() {
      if (this.editedItem.AUTOR_NAME && this.editedItem.NUM_FIGURE) {
        return true;
      }
    },
    // menu() {
    //   return this.navMenu.filter((item) => item.title != "Новости");
    // },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    // if (this.$cookies.get("NAME_AUTOR")) {
    //   this.$set(this.editedItem, "AUTOR_NAME", this.$cookies.get("NAME_AUTOR"));
    // }
    // if (this.$cookies.get("NUM_FIGURE")) {
    //   this.$set(this.editedItem, "NUM_FIGURE", Number(this.$cookies.get("NUM_FIGURE")));
    // }
    this.checkUser();
  },
  methods: {
    ...mapMutations("nsi", ["MUTATE_CURRENT_PEOPLE"]),
    ...mapActions(["SHOW_ERROR", "SHOW_SNACKBAR"]),

    async addCurrentPeople() {
      if (!this.createUser.NUM_PATH || !this.createUser.PEOPLE_CODE) {
        this.SHOW_ERROR("Неправильно указан участник");
        return;
      }
      const confirm = await this.$root.$confirm("Внимание!", "Сохранить текущего участника?", { color: "info" });
      if (!confirm) return;
      this.MUTATE_CURRENT_PEOPLE({ PEOPLE_CODE: this.createUser.PEOPLE_CODE, NUM_PATH: this.createUser.NUM_PATH });
      this.checkUser();
    },
    checkUser() {
      if (this.currentPeople) {
        this.$set(this.editedItem, "NUM_PATH", Number(this.currentPeople.NUM_PATH));
        this.$set(this.editedItem, "PEOPLE_CODE", Number(this.currentPeople.PEOPLE_CODE));
      }
      this.$set(this.editedItem, "TIME_MIN", 0);
      this.$set(this.editedItem, "TIME_SEC", 0);
    },
    async addFinishTimePeople() {
      if (!this.editedItem.NUM_PATH || !this.editedItem.PEOPLE_CODE) {
        this.SHOW_ERROR("Нет текущего участника");
        return;
      }
      if (!this.editedItem.POINTS) {
        this.SHOW_ERROR("Не указаны быллы за заезд");
        return;
      }
      var time = Number(this.editedItem.TIME_MIN) * 60 + Number(this.editedItem.TIME_SEC);
      if (time <= 0) {
        this.SHOW_ERROR("Не указано время");
        return;
      }
      const confirm = await this.$root.$confirm("Внимание!", "Сохранить результат участника?", { color: "info" });
      if (!confirm) return;
      this.$set(this.editedItem, "TIME", time);
      this.MUTATE_CURRENT_PEOPLE({});
      this.editedItem = {};
      this.checkUser();
    },
  },
};
</script>

