        <table class="v-data-table" :height="tableHeight" style="width: 100%">
          <thead class="v-data-table-header">
            <tr :style="{ fontSize: isMobile ? '1em' : '2em' }">
              <th rowspan="1" class="text-center" style="width: 5%">№</th>
              <th rowspan="1" class="text-center" style="width: 55%">ФИО</th>
              <th rowspan="1" class="text-center" style="width: 20%">ОСТ</th>
              <th rowspan="1" class="text-center" style="width: 10%">Время</th>
              <th rowspan="1" class="text-center" style="width: 10%">Баллы</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in resultList" :key="index" :style="{ '--i': '9s', '--index': index + 's', fontSize: isMobile ? '1em' : '1.8em' }">
              <td
                class="px-4 text-center"
                :style="{
                  '--all': resultList.length + 's',
                  '--numCol': '1.8s',
                  '--numRow': index,
                  '--color': color,
                }"
              >
                <div
                  :style="{
                    background:
                      index == 0
                        ? 'linear-gradient(90deg,gold 5%, transparent 80%)'
                        : index == 1
                        ? 'linear-gradient(90deg,silver 5%, transparent 65%)'
                        : index == 2
                        ? 'linear-gradient(90deg,tan 5%, transparent 50%)'
                        : '',
                    width: '100%',
                    height: '100%',
                  }"
                >
                  {{ index + 1 > 3 ? index + 1 : "" }}
                </div>
                <!-- index == 0 ? 'gold' : index == 1 ? 'silver' : index == 2 ? 'tan' : 'none' -->
              </td>
              <td :style="{ '--all': resultList.length + 's', '--numCol': '1.6s', '--numRow': index, '--color': color }" class="px-4 text-center">{{ item.NAME }}</td>
              <td :style="{ '--all': resultList.length + 's', '--numCol': '1.4s', '--numRow': index, '--color': color }" class="px-4 text-center">{{ item.OST }}</td>
              <td :style="{ '--all': resultList.length + 's', '--numCol': '1.2s', '--numRow': index, '--color': color }" class="px-4 text-center">{{ item.TIME }}</td>
              <td :style="{ '--all': resultList.length + 's', '--numCol': '1s', '--numRow': index, '--color': color }" class="px-4 text-center">{{ item.POINTS }}</td>
            </tr>
          </tbody>
        </table>
      </v-col>
      <v-col cols="12" lg="4">
        <h2 class="title">ТЕКУЩИЙ УЧАСТНИК</h2>
        <div class="people" :style="{ minHeight: tableHeight }">
          <div class="people__roundFoto">
            <span></span>
            <span></span>
            <div class="people__roundFoto-wrapper">
              <v-img :src="require('../assets/no-photo.jpg')"></v-img>
            </div>
          </div>
          <h2 class="people__name">ФАМИЛИЯ ИМЯ</h2>
          <h2 class="people__secondName">ОТЧЕСТВО</h2>
          <h2 class="people__profession">профессия</h2>
          <v-list>
            <v-list-item>
              <v-list-item-title>Возраст</v-list-item-title>
              <v-list-item-subtitle>40</v-list-item-subtitle>
            </v-list-item>
            <v-divider dark></v-divider>
            <v-list-item>
              <v-list-item-title>Стаж</v-list-item-title>
              <v-list-item-subtitle>5</v-list-item-subtitle>
            </v-list-item>
            <v-divider dark></v-divider>
            <v-list-item>
              <v-list-item-title>ОСТ</v-list-item-title>
              <v-list-item-subtitle>ТН-Урал</v-list-item-subtitle>
            </v-list-item>
          </v-list>
          <v-divider dark></v-divider>

          <div class="people__nowresult">
            <div class="people__nowresult-header">
              <div class="title">Этап 1</div>
              <div class="title">Этап 2</div>
            </div>
            <div class="people__nowresult-text">
              <div class="text">00:00.0</div>

              <div class="text">00:00.0</div>
            </div>
          </div>
        </div>
      </v-col>
    </v-row>
  </v-card-text>
