<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      color: "gold", //Рандомный цвет
    };
  },
  computed: {
    ...mapState("nsi", ["resultList"]),
    tableHeight() {
      return window.innerHeight - 190 + "px";
    },
    isMobile() {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else return false;
    },
    // menu() {
    //   return this.navMenu.filter((item) => item.title != "Новости");
    // },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    // if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)) {
    //   console.log("Вы используете мобильное устройство (телефон или планшет).");
    // } else console.log("Вы используете ПК.");
    this.newColor();
  },

  methods: {
    // add() {
    //   console.log(this.navMenu);
    // },
    newColor() {
      setInterval(() => {
        this.color = "#" + (Math.random().toString(16) + "000000").substring(2, 4).toUpperCase() + "FBD6";
      }, 24000);
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  text-align: center;
  color: #fff;
  margin: 25px 0;
  font-size: 2.5em !important;
}
.v-card__text {
  .row {
    .col {
      color: white;
    }
  }
}

table th + th {
  border-left: 1px solid #dddddd;
}
table td + td {
  border-left: 1px solid #dddddd;
}
.v-data-table {
  background: #575a7c !important;
  color: #fafbd6 !important;
  thead {
    tr {
      th {
        background: #292b42 !important;
        color: orange !important;
        font-size: 1.1em !important;
        padding: 0 !important;
        height: 40px;
      }
    }
  }
  tbody {
    tr {
      // background: #575a7c !important;
      // animation: tabelRow calc(var(--i) * 0.4 + 5s) infinite;
      // animation-delay: calc(var(--index) * 0.2);

      td {
        animation: tabelCol calc(var(--all) * 0.01 + 24s) infinite;
        animation-delay: calc(var(--numCol) * var(--numRow) * 0.02 + 8s);

        padding: 0 !important;
        border-bottom: 1px solid #292b42;
      }
    }
  }
}
@keyframes tabelRow {
  90% {
    color: #fafbd6;
  }
  95% {
    color: orange;
  }
  100% {
    color: #fafbd6;
  }
}
@keyframes tabelCol {
  98% {
    opacity: 1;
    filter: var(--color);
    color: #fafbd6;
    border-bottom: 1px solid #292b42;
    background: none;
  }
  99% {
    opacity: 0.3;
    color: var(--color); //#82f0ff;
    border-bottom: 1px solid var(--color);
    // background: var(--color);
  }
  100% {
    opacity: 1;
    color: #fafbd6;
    border-bottom: 1px solid #292b42;
    background: none;
  }
}
.people {
  border: 1px solid orange;
  // box-shadow: 0 0 10px 1px orange inset;
  animation: borderPeople 2s infinite alternate;
  padding-top: 35px;
  &__roundFoto {
    position: relative;
    width: 250px;
    height: 250px;
    margin: 0 auto;
    &-wrapper {
      position: relative;
      box-shadow: 0 0 15px 5px #fff;
      width: 250px;
      height: 250px;
      border-radius: 50%;
      background: #fff;
      margin: 0 auto;
      overflow: hidden;
    }
    // .forward {
    // }
    // .back {
    // }
    span {
      position: absolute;
      top: 0;
      left: 0;
      // background: red;
      width: 250px;
      height: 250px;
      &:not(:last-child) {
        animation: 8s linear 0s normal none infinite running spinSpan;
      }
      &:first-child {
        animation: 8s linear reverse 0s none infinite running spinSpan;
      }
      &::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 100px;
        width: 50px;
        height: 2px;
        border-radius: 50%;
        background: #fff;
        box-shadow: 0 0 30px 10px #fff;
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 2px;
        left: 100px;
        width: 50px;
        height: 2px;
        border-radius: 50%;
        background: #fff;
        box-shadow: 0 0 30px 10px #fff;
      }
    }
    @keyframes spinSpan {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }
  &__name {
    margin-top: 35px;
    font-size: 2em;
    color: #fafbd6;
    text-align: center;
  }
  &__secondName {
    margin-top: 15px;
    font-size: 1.4em;
    color: #fafbd6;
    text-align: center;
  }
  &__profession {
    margin-top: 10px;
    font-size: 1.2em;
    color: #fafbd6;
    text-align: center;
  }
  &__nowresult {
    margin-bottom: 15px;
    &-header {
      .title {
        font-size: 1.5em;
        color: orange;
        width: 50%;
        display: inline-block;
        text-align: center;
      }
    }
    &-text {
      .text {
        font-size: 1.1em;
        color: #fafbd6;
        width: 50%;
        display: inline-block;
        text-align: center;
      }
    }
  }
}
@keyframes borderPeople {
  0% {
    box-shadow: 0 0 3px 1px #ffbf48 inset;
  }
  100% {
    box-shadow: 0 0 15px 1px #ffbf48 inset;
  }
}

.v-list {
  background: none !important;
  &-item {
    &__title {
      color: #fafbd6 !important;
      text-align: start;
    }
    &__subtitle {
      color: #fafbd6 !important;
      text-align: end;
    }
  }
}
