<template>
  <v-snackbar :value="snackbarParams.active" @change="change()" :color="snackbarParams.color" :timeout="snackbarParams.timeout || 3000" bottom multi-line shaped>{{ snackbarParams.text }}</v-snackbar>
</template>

<script>
export default {
  computed: {
    snackbarParams() {
      return this.$store.state.snackbarParams;
    },
  },
  watch: {
    snackbarParams(val) {
      if (val.active) {
        setTimeout(() => {
          this.$store.commit("MUTATE_SNACKBAR_PARAMS", { color: "", text: "", active: false });
        }, this.snackbarParams.timeout);
      }
    },
  },
};
</script>
