export default {
  // Запрос возвращает данные по пользователю (UserId, Филиал, Отдел)

  /**
   * Вывыод ошибки в снэкбаре
   */
  SHOW_ERROR({ commit }, error) {
    const errorMessage = error.response ? `${error} | ${error.response.data.message} | ` : error;
    commit("MUTATE_SNACKBAR_PARAMS", { color: "error", text: errorMessage, active: true, timeout: 10000 });
    return false;
  },

  /**
   * Покзавает всплывающее сообщение с переданным в него текстом
   */
  SHOW_SNACKBAR({ commit }, massage) {
    commit("MUTATE_SNACKBAR_PARAMS", { color: "success", text: massage, active: true, timeout: 3000 });
  },

  /**
   * Покзавает всплывающее информационно сообщение с переданным в него текстом
   */
  SHOW_INFO_SNACKBAR({ commit }, massage) {
    commit("MUTATE_SNACKBAR_PARAMS", { color: "info", text: massage, active: true, timeout: 3000 });
  },
};
